/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.noMargin {
  margin: 0;
}

.propertyDescriptionContainer {
  display: flex;
  justify-content: space-between;
}

.characteresCount {
  display: flex;
  align-items: center;
}

#current {
  margin-right: 1rem;
}

.characters {
  font-size: 10px;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contentCenter {
  max-width: 80rem;
}

.rigthAlign {
  display: flex;
  justify-content: flex-end;
}

.yellowButton {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.yellowButton:hover {
  background-color: #c77808;
  cursor: pointer;
}

.yellowButtonBlock {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.yellowButtonBlock:hover {
  background-color: #c77808;
  cursor: pointer;
}

.greenButton {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.greenButton:hover {
  background-color: #619800;
  cursor: pointer;
}

.greenButtonBlock {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.greenButtonBlock:hover {
  background-color: #619800;
  cursor: pointer;
}

.redButtonBlock {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.redButtonBlock:hover {
  background-color: #a40606;
  cursor: pointer;
}

.blueButtonBlock {
  background-color: #1575E1;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.blueButtonBlock:hover {
  background-color: #1369ca;
  cursor: pointer;
}

.darkMode {
  background-color: #1a1a1a;
}
.darkMode p {
  color: #FFFFFF;
}
.darkMode a,
.darkMode h1,
.darkMode h2,
.darkMode h3,
.darkMode h4 {
  color: #e1e1e1;
}
.darkMode .advertisements,
.darkMode .propertyResume {
  background-color: #333333;
  border: 1px solid #333333;
}
.darkMode .advertisements .price,
.darkMode .propertyResume .price {
  color: #71B100;
}
.darkMode .advertisements .icon,
.darkMode .propertyResume .icon {
  filter: invert(100%);
}
.darkMode .aboutText blockquote {
  color: #FFFFFF;
}
.darkMode .form label,
.darkMode .form legend,
.darkMode .form p {
  color: #e1e1e1;
}
.darkMode .form input:not([type=submit]),
.darkMode .form textarea,
.darkMode .form select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.darkMode .propertyDescriptionContainer {
  color: white;
}

.no-webp .home {
  background-image: url(../img/header.jpg);
}

.webp .home {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.home .headerContent {
    min-height: 70rem;
  }
}

.headerContent {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.headerContent h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footerContainer {
  padding-top: 2.5rem;
}
.footer .nav {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.rigth {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .rigth {
    align-items: flex-end;
  }
}
.rigth .darkModeBtn {
  width: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
.rigth .darkModeBtn:hover {
  cursor: pointer;
  filter: invert(90%);
}
@media (min-width: 768px) {
  .rigth .darkModeBtn {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.navbar img {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .navbar img {
    margin-bottom: 0;
  }
}

.nav {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    opacity: 1;
  }
}
.nav a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .nav a {
    margin-right: 2rem;
    font-size: 1.8rem;
    visibility: visible;
  }
  .nav a:last-of-type {
    margin-right: 0;
  }
}

/*Mobile Menu*/
@media (min-width: 768px) {
  .mobileMenu {
    display: none;
  }
}
.mobileMenu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .aboutIcons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .advertisementsContainer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advertisements {
  background-color: #eeeeee;
  border: 1px solid #1a1a1a;
  margin-bottom: 3rem;
}
.advertisements .advertisementsContent {
  padding: 2rem;
}
.advertisements h3,
.advertisements p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.characteristicsIcons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.characteristicsIcons li {
  flex: 1;
  display: flex;
}
.characteristicsIcons li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.characteristicsIcons li p {
  font-weight: 900;
}

.propertyResume {
  padding: 2rem;
}

.no-webp .contactImg {
  background-image: url(../img/encuentra.jpg);
}

.webp .contactImg {
  background-image: url(../img/encuentra.webp);
}

.contactImg {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contactImg h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 900;
}
.contactImg p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .lowerSection {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blogEntry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blogEntry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blogEntry:last-of-type {
  margin-bottom: 0;
}

.metaInfo span {
  color: #E08709;
}

.entryText a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .entryText a {
    margin-top: 0;
  }
}
.entryText h4 {
  margin: 0;
  line-height: 1.4;
}
.entryText h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimony {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimony blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimony blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: 0;
}
.testimony p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .contactForm {
  max-width: 40rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .aboutContent {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.aboutText blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding: 1rem 0 3rem 0;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0.1rem 0;
  border-radius: 0.5rem;
}
.alert.error {
  background-color: rgb(189, 7, 7);
}
.alert.correct {
  background-color: #71B100;
}

table.properties {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background-color: #71B100;
}
table.properties thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.properties .tableImage {
  width: 10rem;
}

.smallImg {
  width: 20rem;
}